<h1 id="安装" tabindex="-1"><a class="header-anchor" href="#安装" aria-hidden="true">#</a> 安装</h1>
<p>在安装之前，先要安装<code>Node.js</code>。当前<code>LTS版本</code>是比较理想的选择，如果安装比较老的版本会带来一些不必要的麻烦</p>
<p>官方文档提供来两种安装方式，一种是<code>本地安装</code>，一种是<code>全局安装</code>。
!&gt; 不推荐使用全局安装，全局安装会锁定特定版本的web包，可能会在使用不同版本的项目中失败。</p>
<p>安装最新版本</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>npm install <span class="token operator">--</span>save<span class="token operator">-</span>dev webpack
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>安装指定版本</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>npm install <span class="token operator">--</span>save<span class="token operator">-</span>dev webpack<span class="token operator">&lt;</span>version<span class="token operator">></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="custom-container warning"><p class="custom-container-title">WARNING</p>
<p>本地安装是大多数项目所推荐的。这使得在引入重大更改时，可以轻松的单独升级项目。</p>
</div>
<h2 id="npm" tabindex="-1"><a class="header-anchor" href="#npm" aria-hidden="true">#</a> npm</h2>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token string">"scripts"</span><span class="token operator">:</span> <span class="token punctuation">{</span>
	<span class="token string">"build"</span><span class="token operator">:</span> <span class="token string">"webpack --config webpack.config.js"</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>npx webpack
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>